// export default Input;


import React, { useContext, useState } from 'react';
import { AuthContext } from '../context/AuthContext';
import { ChatContext } from '../context/ChatContext';
import camera from '../Images/picture.png'; // Assuming this is your camera icon
import attach from '../Images/setting2.png'; // Assuming this is your attachment icon

// Import specific Realtime Database functions
import { ref as dbRef, push, serverTimestamp, update } from 'firebase/database';
// Import specific Storage functions
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { db, storage } from '../Firebase'; // Assuming 'db' is Realtime DB instance, 'storage' is Cloud Storage instance
import { v4 as uuid } from 'uuid'; // For generating unique IDs for image filenames

const Input = () => {
  const [text, setText] = useState('');
  const [img, setImg] = useState(null);
  const [error, setError] = useState(null);
  const [isSending, setIsSending] = useState(false); // New state to prevent multiple sends

  const { currentUser } = useContext(AuthContext);
  const { data } = useContext(ChatContext); // data.chatId and data.user (recipient info)

  const handleSend = async () => {
    // Prevent sending if already in process or if no recipient is selected
    if (isSending || !data.chatId || !data.user?.uid) {
      console.warn("Attempted to send message without recipient or while sending.");
      return;
    }

    // Basic validation: Don't send completely empty messages
    if (!text.trim() && !img) {
      setError("Please type a message or select an image to send.");
      return;
    }

    setIsSending(true); // Set sending state to true
    setError(null); // Clear any previous errors

    try {
      let downloadURL = null;
      let lastMessageText = text.trim(); // Default last message text

      if (img) {
        // 1. Upload the image to Cloud Storage
        // Use uuid for a unique filename to prevent clashes
        const fileName = `${uuid()}_${img.name}`;
        const storagePath = `chat_images/${data.chatId}/${currentUser.uid}/${fileName}`;
        const imageRef = storageRef(storage, storagePath);

        await uploadBytes(imageRef, img);
        downloadURL = await getDownloadURL(imageRef);
        console.log("Image uploaded to Cloud Storage:", downloadURL);

        // If an image was sent, update the last message text accordingly
        if (!lastMessageText) { // If there's no text with the image
            lastMessageText = "[Image]";
        }
      }

      // 2. Prepare the message object for Realtime Database
      const message = {
        // Firebase `push` will generate its own ID for the message,
        // so we don't need a `uuid()` here unless you have a specific reason.
        text: lastMessageText, // Use the adjusted last message text
        senderId: currentUser.uid,
        date: serverTimestamp(), // Use Realtime Database's serverTimestamp for ordering
        ...(downloadURL && { img: downloadURL }), // Only include img field if an image was uploaded
      };

      // 3. Push the message to the Realtime Database under the specific chat's messages node
      const messagesListRef = dbRef(db, `chats/${data.chatId}/messages`);
      await push(messagesListRef, message); // `push` automatically generates a unique key for the message

      console.log("Message successfully pushed to Realtime DB:", message);

      // 4. Update the 'userChats' for both sender and recipient
      // This updates the last message and date displayed in the chat list
      const updateData = {
        lastMessage: {
          text: lastMessageText,
          senderId: currentUser.uid,
        },
        date: serverTimestamp(),
      };

      // Update sender's userChats
      await update(dbRef(db, `userChats/${currentUser.uid}/${data.chatId}`), updateData);
      // Update recipient's userChats
      await update(dbRef(db, `userChats/${data.user.uid}/${data.chatId}`), updateData);

      console.log("userChats updated for sender and recipient.");

      // 5. Clear the input fields and reset state after successful send
      setText('');
      setImg(null);
      setError(null);

    } catch (err) {
      console.error("Error sending message:", err);
      setError("Failed to send message. Please try again. " + err.message);
    } finally {
      setIsSending(false); // Always reset sending state
    }
  };

  return (
    <div className='input'>
      {error && <p className="error-message">{error}</p>}
      <input
        type='text'
        placeholder='Type a message...'
        onChange={(e) => setText(e.target.value)}
        value={text} // Bind value for a controlled component
        disabled={isSending || !data.chatId} // Disable if sending or no chat selected
      />
      <div className='send'>
        {/* Attach File (e.g., non-image files) */}
        <label htmlFor='file'>
          <img src={attach} alt='Attach' className='camera' />
          <input
            type='file'
            style={{ display: 'none' }}
            id='file'
            onChange={(e) => { setImg(e.target.files[0]); setError(null); }}
            disabled={isSending || !data.chatId}
          />
        </label>
        {/* Attach Image (specific for images) */}
        <label htmlFor='image-file'>
          <img src={camera} alt='Camera' className='camera' />
          <input
            type='file'
            style={{ display: 'none' }}
            id='image-file'
            accept="image/*" // Restrict to image files
            onChange={(e) => { setImg(e.target.files[0]); setError(null); }}
            disabled={isSending || !data.chatId}
          />
        </label>
        {/* Display selected image name and offer to clear it */}
        {img && (
          <span className="image-preview-name">
            {img.name}
            <button className="clear-image-btn" onClick={() => setImg(null)}>x</button>
          </span>
        )}
        <button
          className='btn btn-success'
          onClick={handleSend}
          disabled={isSending || (!text.trim() && !img) || !data.chatId}
        >
          {isSending ? "Sending..." : "Send"}
        </button>
      </div>
    </div>
  );
};

export default Input;
