// export default Messages


import React, { useContext, useEffect, useState } from 'react';
import Message from './Message';
import { ChatContext } from '../context/ChatContext';
import { onValue, ref } from 'firebase/database'; // Import onValue and ref
import { db } from '../Firebase'; // Assuming 'db' is your Realtime Database instance

const Messages = () => {
  const [messages, setMessages] = useState([]);
  const { data } = useContext(ChatContext);

  useEffect(() => {
    // Only set up listener if chatId exists
    if (!data.chatId) return;

    // Construct the reference to the specific chat's messages
    // Assuming your messages are directly under 'Chats/chatId' as an array or object
    // If your messages are under a 'messages' sub-node, the path would be `Chats/${data.chatId}/messages`
    const chatMessagesRef = ref(db, `chats/${data.chatId}/messages`); // Adjust path if needed

    // Use onValue to listen for changes to the data at this reference
    const unsubscribe = onValue(chatMessagesRef, (snapshot) => {
      if (snapshot.exists()) {
        const messagesData = snapshot.val();
        // Realtime Database often returns data as an object of objects
        // If your messages are stored as an object (e.g., { messageId1: {...}, messageId2: {...} }),
        // you'll need to convert it to an array to map over it.
        const messagesArray = Object.keys(messagesData).map(key => ({
          id: key, // Use the key as the message ID
          ...messagesData[key]
        }));
        setMessages(messagesArray);
      } else {
        setMessages([]); // No messages yet for this chat
      }
    });

    // Clean up the listener when the component unmounts or chatId changes
    return () => {
      unsubscribe();
    };
  }, [data.chatId]); // Re-run effect when chatId changes

  console.log(messages);

  return (
    <div className='messages'>
      {messages.map((m) => (
        <Message message={m} key={m.id} /> // Changed 'messages' prop to 'message' for clarity
      ))}
    </div>
  );
};

export default Messages;
