// export default Login


import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';
import React, { useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
// Make sure your Firebase app is initialized somewhere, e.g., in `../Firebase.js`
// and you're exporting the initialized `auth` instance, or just call `getAuth()`
// which uses the default initialized app.

const Login = () => {
  const [email, setEmail] = useState(''); // Initialize with empty string
  const [password, setPassword] = useState(''); // Initialize with empty string
  const [error, setError] = useState(null); // Use 'error' to store the error message
  const [loading, setLoading] = useState(false); // State to show loading indicator
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault(); // Prevent default form submission behavior
    setError(null);    // Clear previous errors
    setLoading(true); // Set loading state

    try {
      const auth = getAuth(); // Get the Firebase Auth instance
      const userCredential = await signInWithEmailAndPassword(auth, email, password);

      // If login is successful, userCredential.user contains the authenticated user.
      // You can do something with the user object if needed, e.g., save it to context.
      // console.log("Login successful!", userCredential.user);

      // Redirect the user to the home page or dashboard
      navigate('/');
    } catch (err) {
      // Handle different Firebase Auth errors
      let errorMessage = 'An unexpected error occurred. Please try again.';
      switch (err.code) {
        case 'auth/invalid-email':
          errorMessage = 'The email address is not valid.';
          break;
        case 'auth/user-disabled':
          errorMessage = 'This account has been disabled.';
          break;
        case 'auth/user-not-found':
        case 'auth/wrong-password':
          errorMessage = 'Invalid email or password.';
          break;
        // You can add more specific error codes if needed
        default:
          console.error("Firebase Auth Error:", err.code, err.message);
          errorMessage = err.message || errorMessage; // Fallback to Firebase's message
      }
      setError(errorMessage);
    } finally {
      setLoading(false); // Always stop loading, whether success or failure
    }
  };

  return (
    <div className='d-flex justify-content-center align-items-center bg-body-secondary vh-100'>
      <div className='p-3 shadow mb-5 w-25 rounded-1 bg-light'>
        <h2>Login</h2>
        <form onSubmit={handleSubmit}>
          <div className='mb-3'>
            <label htmlFor='email'><strong>Email</strong></label>
            <input
              type='email'
              onChange={(e) => setEmail(e.target.value)}
              value={email} // Controlled component
              placeholder='Enter Email'
              name='email'
              autoComplete='email' // Better autocomplete hint
              className='form-control rounded-1'
              required // HTML5 validation
            />
          </div>
          <div className='mb-3'>
            <label htmlFor='password'><strong>Password</strong></label>
            <input
              type='password'
              onChange={(e) => setPassword(e.target.value)}
              value={password} // Controlled component
              placeholder='Enter Password'
              name='password'
              autoComplete='current-password' // Better autocomplete hint
              className='form-control rounded-1'
              required // HTML5 validation
            />
          </div>
          <button
            type='submit'
            className='btn btn-success border-1 w-100'
            disabled={loading} // Disable button while loading
          >
            {loading ? 'Logging in...' : 'Login'}
          </button>
          {error && <p className='text-danger mt-2'>{error}</p>} {/* Display specific error */}
        </form>
        <p className='mt-3'>Don't have an account?</p>
        <NavLink to='/register' className={'btn btn-default border w-100'}>Register</NavLink>
      </div>
    </div>
  );
};

export default Login;
