import user from "../Images/user.png"
import React, { useContext, useState } from 'react';
import { getDatabase, ref, query, orderByChild, equalTo, get, set, update, serverTimestamp } from 'firebase/database';
import { getAuth } from 'firebase/auth'; // To potentially exclude the current user from search results
import { AuthContext } from "../context/AuthContext";
import { db } from "../Firebase";
// import { getDatabase, ref, get, set, update, serverTimestamp } from "firebase/database";

// Assuming 'db' from '../Firebase' is your initialized Firebase App instance,
// or you can call getDatabase() without arguments if it's the default app.

const Search = () => {
  const [username, setUsername] = useState("");
  const [user, setUser] = useState(null);
  const [err, setErr] = useState(false);

  const {currentUser} = useContext(AuthContext)

  const handleSearch = async () => {
    setErr(false); // Reset error state for new search
    setUser(null); // Clear previous user result

    // Get the Realtime Database instance
    const dbInstance = getDatabase();
    // Reference to the 'users' node in your database
    const usersRef = ref(dbInstance, "users");

    // Construct a Realtime Database query:
    // Order the 'users' by their 'displayName' child, then find the one
    // where 'displayName' is exactly equal to the entered username.
    const userQuery = query(usersRef, orderByChild("displayName"), equalTo(username));

    try {
      // Execute the query to get data once
      const snapshot = await get(userQuery);

      if (snapshot.exists()) {
        const auth = getAuth();
        const currentUserUid = auth.currentUser ? auth.currentUser.uid : null;

        const usersData = snapshot.val(); // Returns an object where keys are UIDs
        let foundUser = null;

        // Iterate through the results to find a match and potentially exclude self
        for (const userId in usersData) {
          if (usersData.hasOwnProperty(userId)) {
            // Optional: You might want to exclude the currently logged-in user
            if (currentUserUid && userId === currentUserUid) {
              continue; // Skip the current user if they are searching for themselves
            }
            foundUser = { uid: userId, ...usersData[userId] };
            break; // Assuming displayName is unique, or you want the first match
          }
        }

        if (foundUser) {
          setUser(foundUser);
        } else {
          setErr(true); // User not found, or only self was found
        }
      } else {
        // No user found with the given displayName
        setErr(true);
      }
    } catch (error) {
      console.error("Error searching for user:", error);
      setErr(true);
      setUser(null);
    }
  };

  const handleKey = (e) => {
    e.code === "Enter" && handleSearch();
  };


  // chat
  
const handleSelect = async () => {
  // Always good to add a quick check for essential data before proceeding
  if (!currentUser || !user || !currentUser.uid || !user.uid) {
    console.error("Missing current user or selected user data.");
    return;
  }

  // Create a unique combined ID for the chat between two users
  const combinedId =
    currentUser.uid > user.uid
      ? currentUser.uid + user.uid
      : user.uid + currentUser.uid;

  // Get the Realtime Database instance
  const db = getDatabase();

  try {
    // 1. Check if the chat room already exists in the "Chats" node
    const chatRef = ref(db, "Chats/" + combinedId);
    const chatSnapshot = await get(chatRef); // Use 'get' to fetch data once

    if (!chatSnapshot.exists()) {
      // 2. If the chat room doesn't exist, create it
      await set(chatRef, {
        messages: [], // Initialize with an empty messages array
      });

      // 3. Create/update the chat entry for the current user
      const currentUserChatsRef = ref(db, "userChats/" + currentUser.uid);
      await update(currentUserChatsRef, {
        [combinedId + "/userInfo"]: {
          uid: user.uid,
          displayName: user.displayName,
          photoURL: user.photoURL,
        },
        [combinedId + "/date"]: serverTimestamp(), // Use serverTimestamp for accurate time
      });

      // 4. Create/update the chat entry for the selected user (reciprocal)
      const selectedUserChatsRef = ref(db, "userChats/" + user.uid);
      await update(selectedUserChatsRef, {
        [combinedId + "/userInfo"]: {
          uid: currentUser.uid,
          displayName: currentUser.displayName,
          photoURL: currentUser.photoURL,
        },
        [combinedId + "/date"]: serverTimestamp(),
      });

      console.log(`New chat "${combinedId}" created successfully!`);
    } else {
      console.log(`Chat "${combinedId}" already exists. Navigating to existing chat.`);
      // Optionally, you might want to navigate to the existing chat here
    }
  } catch (error) {
    console.error("Error handling user selection and chat creation:", error);
  }
  setUser(null)
  setUsername("")
};

  // chats
  return (
    <div className='search'>
      <div className='searchForm'>
        <input
          type='text'
          placeholder='Search...'
          onKeyDown={handleKey}
          onChange={(e) => setUsername(e.target.value)}
          value={username} // Make the input a controlled component
        />
      </div>
      {err && <span>User not found!</span>}

      {user && (
        <div className='userChat' onClick={handleSelect}>
          {/* Ensure photoURL exists, provide a fallback if it doesn't */}
          <img src={user.photoURL || user} alt={user.displayName} className='userProfile' />
          <div className='userChatInfo'>
            <span>{user.displayName}</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default Search;
