import React, { useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import userPlaceholder from '../Images/user.png'; 
import './Register.css'; 
import { createUserWithEmailAndPassword, updateProfile } from 'firebase/auth';
import { auth, db, storage } from '../Firebase'; 
import { ref as databaseRef, set } from "firebase/database"; 
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage'; 
import { v4 } from 'uuid'; 

const Register = () => {
    const [err, setErr] = useState(false);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();


    const [displayName, setDisplayName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [bio, setBio] = useState('');
    const [profileImageFile, setProfileImageFile] = useState(null); 
    const [previewImageUrl, setPreviewImageUrl] = useState(userPlaceholder); 

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setProfileImageFile(file);
            setPreviewImageUrl(URL.createObjectURL(file)); 
        } else {
            setProfileImageFile(null);
            setPreviewImageUrl(userPlaceholder); 
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); 
        setLoading(true);   
        setErr(false);      

        try {
           
            const userCredential = await createUserWithEmailAndPassword(auth, email, password);
            const user = userCredential.user; 

            let photoURL = ''; 

            if (profileImageFile) {
                const imagePath = `profile_pictures/${user.uid}/${v4()}_${profileImageFile.name}`;
                const imageReference = storageRef(storage, imagePath);
                await uploadBytes(imageReference, profileImageFile); 
                photoURL = await getDownloadURL(imageReference);    
                console.log("Profile picture uploaded:", photoURL);
            }

            await updateProfile(user, {
                displayName: displayName,
                photoURL: photoURL
            });
            console.log("Firebase Auth profile updated.");

            await set(databaseRef(db, "users/" + user.uid), {
                uid: user.uid,
                displayName: displayName,
                email: email,
                bio: bio,
                photoURL: photoURL, 
                password: password,
                createdAt: new Date().toISOString() 
            });
            console.log("User data saved to Realtime Database.");
            await set(databaseRef(db, "Chats/" + user.uid),{
                uid: user.uid,
                
            })
            
            navigate('/');

        } catch (error) {
            console.error("Error during registration:", error);
            setErr(true); 
        } finally {
            setLoading(false); 
        }
    };

    return (
        <div className='d-flex justify-content-center align-items-center vh-100 bg-body-secondary'>
            <div className='p-3 mb-3 shadow w-25 bg-light rounded-2'>
                <h2>Register</h2>
                <form onSubmit={handleSubmit}>

                    <div className='mb-1 p-1'>
                        <label htmlFor='displayName'><strong>Display Name</strong></label>
                        <input
                            type='text'
                            onChange={(e) => setDisplayName(e.target.value)}
                            value={displayName} // Controlled component
                            placeholder='Enter Display Name'
                            name='displayName'
                            autoComplete='name'
                            className='form-control rounded-1'
                            required // This field is mandatory
                            disabled={loading}
                        />
                    </div>
                    <div className='mb-1 p-1'>
                        <label htmlFor='email'><strong>Email </strong></label>
                        <input
                            type='email'
                            onChange={(e) => setEmail(e.target.value)}
                            value={email} // Controlled component
                            placeholder='Enter Email'
                            name='email'
                            autoComplete='email'
                            className='form-control rounded-1'
                            required // This field is mandatory
                            disabled={loading}
                        />
                    </div>
                    <div className='mb-1 p-1'>
                        <label htmlFor='password'><strong>Password</strong></label>
                        <input
                            type='password'
                            onChange={(e) => setPassword(e.target.value)}
                            value={password} // Controlled component
                            placeholder='Enter Password (min 6 characters)'
                            name='password'
                            autoComplete='new-password'
                            className='form-control rounded-1'
                            required // This field is mandatory
                            disabled={loading}
                        />
                    </div>

                    {/* <div className='mb-3 p-1'>
                        <label htmlFor='bio'><strong>User Bio</strong></label>
                        <input
                            type='text'
                            onChange={(e) => setBio(e.target.value)}
                            value={bio} // Controlled component
                            placeholder='Enter a short bio (optional)'
                            name='bio'
                            autoComplete='off'
                            className='form-control rounded-1'
                            disabled={loading}
                        />
                    </div> */}

                    {/* Profile Picture Upload Section */}
                    <div className='profile text-center'>
                        <input
                            type='file'
                            onChange={handleFileChange}
                            name='profilePic'
                            hidden
                            id='profilePicInput'
                            accept="image/*" 
                            disabled={loading}
                        />
                        <label htmlFor='profilePicInput' style={{ cursor: 'pointer' }}>
                            <img
                                src={previewImageUrl}
                                alt='User Profile'
                                className='userProfile'
                            />
                            <p>Click to add profile picture</p>
                        </label>
                    </div>

                    <button disabled={loading} type="submit" className='btn btn-success border-1 w-100'>
                        {loading ? 'Registering...' : 'Register'}
                    </button>

                    {loading && <p className="text-info">Creating your account...</p>}
                    {err && <p className='text-danger'>Something went wrong! Please check your details and try again.</p>}
                </form>
                <p>Already have an account?</p>
                <NavLink to='/login' className='btn btn-outline-secondary w-100'>Login</NavLink>

            </div>
        </div>
    );
};

export default Register;
