import React, { useContext, useEffect, useState } from 'react';
import { getDatabase, ref, onValue } from 'firebase/database';
import { AuthContext } from '../context/AuthContext'; // Make sure AuthContext provides currentUser
import { ChatContext } from '../context/ChatContext';

const Chats = () => {
  const [chats, setChats] = useState({}); // State for the current user's active chats
  const [allUsers, setAllUsers] = useState({}); // New state for all other users in the system
  const { currentUser } = useContext(AuthContext);
  const {dispatch} = useContext(ChatContext)

  useEffect(() => {
    if (currentUser && currentUser.uid) {
      const database = getDatabase(); // Get the Realtime Database instance
      const userChatsRef = ref(database, `userChats/${currentUser.uid}`);

      // Set up the real-time listener for current user's chats
      const unsubscribeChats = onValue(userChatsRef, (snapshot) => {
        const data = snapshot.val();
        setChats(data || {}); // Set the data, or an empty object if no data
      }, (error) => {
        console.error("Error fetching user's active chats:", error);
        // You might want to display an error message in the UI
      });

      // Cleanup function for this specific listener
      return () => unsubscribeChats();
    } else {
      setChats({}); // Clear chats if no user is logged in
    }
  }, [currentUser?.uid]); // Re-run this effect if the current user's UID changes

  // --- Effect 2: Fetch ALL users (excluding the current user) ---
  useEffect(() => {
    if (currentUser?.uid) {
      const database = getDatabase(); // Get the Realtime Database instance
      const usersRef = ref(database, 'users'); // Reference to your root 'users' node

      // Set up the real-time listener for all users
      const unsubscribeAllUsers = onValue(usersRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          // Filter out the current user from the list of all users
          const filteredUsers = Object.fromEntries(
            Object.entries(data).filter(([uid]) => uid !== currentUser.uid)
          );
          setAllUsers(filteredUsers);
        } else {
          setAllUsers({}); // No other users found
        }
      }, (error) => {
        console.error("Error fetching all users:", error);
      });

      // Cleanup function for this specific listener
      return () => unsubscribeAllUsers();
    } else {
      setAllUsers({}); // Clear all users if no user is logged in
    }
  }, [currentUser?.uid]); // Re-run this effect if the current user's UID changes

  console.log("Current user's active chats:", chats);
  console.log("All other users available:", allUsers);

  // This function would be called when a user clicks on an 'allUsers' entry
  const handleSelectUser = (userUid, userData) => {
    console.log(`User ${currentUser.displayName} wants to chat with: ${userData.displayName} (UID: ${userUid})`);
  
  };
    const handleSelect = (u)=>{
    dispatch({type:"CHANGE_USER", payload:u})
  }

  return (
    <div className='chats-main-container'> {/* A container for both sections */}

      <div className='active-chats-section'>
        {Object.keys(chats).length > 0 ? (
          Object.entries(chats)
            // Sort chats, assuming 'date' is a timestamp in your chatData
            .sort((a, b) => (b[1].date || 0) - (a[1].date || 0))
            .map(([chatId, chatData]) => (
              <div className='userChat' key={chatId} onClick={()=> handleSelect(chatData.userInfo)}>
                <img
                  src={chatData.userInfo?.photoURL || 'https://via.placeholder.com/50/CCCCCC/FFFFFF?text=P'}
                  alt='chat partner'
                />
                <div className='userChatInfo'>
                  <span>{chatData.userInfo?.displayName || 'Unknown Chat'}</span>
                  <p>{chatData.lastMessage?.text || 'No messages yet'}</p>
                </div>
              </div>
            ))
        ) : (
          <p className='no-chats-message'>You don't have any active chats yet.</p>
        )}
      </div>

      {/* <div className='all-users-section'>
        {Object.keys(allUsers).length > 0 ? (
          Object.entries(allUsers).map(([uid, userData]) => (
            // Make the entire user entry clickable to start a chat
            <div className='userChat' key={uid} onClick={() => handleSelectUser(uid, userData)}>
              <img
                src={userData.photoURL }
                alt='user'
              />
              <div className='userChatInfo'>
                <span>{userData.displayName}</span>
              </div>
            </div>
          ))
        ) : (
          <p className='no-users-message'>No other users to show right now.</p>
        )}
      </div> */}
    </div>
  );
};

export default Chats;

