

import React, { useState, useEffect } from 'react';
import userPlaceholder from '../Images/user.png'; 
import { storage, auth, db } from '../Firebase'; 
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { updateProfile as updateAuthProfile } from 'firebase/auth'; 
import { ref as databaseRef, set, get } from 'firebase/database'; 
import { v4 } from 'uuid'; 
import { useNavigate } from 'react-router-dom';

const UpdateProfile = () => {
    const [bio, setBio] = useState('');
    const [profileImageFile, setProfileImageFile] = useState(null); 
    const [displayProfileImageUrl, setDisplayProfileImageUrl] = useState(userPlaceholder); 
    const [name, setName] = useState('');
    const navigate = useNavigate()
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [successMessage, setSuccessMessage] = useState(null);

    useEffect(() => {
        const currentUser = auth.currentUser;
        if (currentUser) {
            setName(currentUser.displayName || '');
            setDisplayProfileImageUrl(currentUser.photoURL || userPlaceholder);

            const userBioRef = databaseRef(db, `users/${currentUser.uid}/bio`);
            get(userBioRef).then((snapshot) => {
                if (snapshot.exists()) {
                    setBio(snapshot.val());
                }
            }).catch(err => {
                console.error("Error fetching user bio:", err);
                setError("Failed to load bio.");
            });
        } else {
            setError("No user logged in. Please log in to update your profile.");
        }
    }, []); 

    
    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setProfileImageFile(file);
            setDisplayProfileImageUrl(URL.createObjectURL(file)); 
        }
    };

   
    const handleUpdateProfile = async (e) => {
        e.preventDefault(); 
        setLoading(true);
        setError(null);
        setSuccessMessage(null);

        const currentUser = auth.currentUser;
        if (!currentUser) {
            setError("No user logged in. Please log in to update your profile.");
            setLoading(false);
            return;
        }

        let newPhotoURL = currentUser.photoURL; 

        try {
        
            if (profileImageFile) {
              
                const imagePath = `profile_pictures/${currentUser.uid}/${v4()}_${profileImageFile.name}`;
                const imageReference = storageRef(storage, imagePath);

                await uploadBytes(imageReference, profileImageFile);
                newPhotoURL = await getDownloadURL(imageReference); 
                console.log("New profile image uploaded:", newPhotoURL);
            }

            const authUpdateData = {};
            if (name !== currentUser.displayName) { 
                authUpdateData.displayName = name;
            }
            if (newPhotoURL && newPhotoURL !== currentUser.photoURL) { 
                authUpdateData.photoURL = newPhotoURL;
            }

            if (Object.keys(authUpdateData).length > 0) {
                 await updateAuthProfile(currentUser, authUpdateData);
                 console.log("Firebase Auth profile updated.");
            }

          
            const userBioReference = databaseRef(db, `users/${currentUser.uid}/bio`);
            await set(userBioReference, bio);
            console.log("User bio updated in Realtime Database.");

            setSuccessMessage("Profile updated successfully!");
            navigate('/')

        } catch (err) {
            console.error("Error updating profile:", err);
            setError(`Failed to update profile: ${err.message}`);
        } finally {
            setLoading(false); 
        }
    };

  return (
    <div className='d-flex justify-content-center align-items-center bg-body-secondary vh-100 flex-column'>
      <div className='shadow mb-3 p-5 rounded-1 w-50'>
      <h2>Update Profile</h2>
      {loading && <p className="text-info">Updating profile...</p>}
      {error && <p className="text-danger">{error}</p>}
      {successMessage && <p className="text-success">{successMessage}</p>}
      <form onSubmit={handleUpdateProfile} className='d-flex justify-content-center align-items-center flex-column'>
        <div>
            <input type='file' onChange={handleFileChange} hidden id='file'/>
            <label htmlFor='file'>
            <img
                src={displayProfileImageUrl}
                alt='User Profile'
                className='userProfile'
                style={{ cursor: 'pointer', width: '100px', height: '100px', borderRadius: '50%', objectFit: 'cover' }}
            />
            </label>
          <p>Click to change profile picture</p>
        </div>

            <input
                type='text'
                onChange={(e) => setName(e.target.value)}
                value={name} 
                placeholder='Enter name'
                name='name'
                className='userName'
                disabled={loading} 
            />
            <textarea
                placeholder='User Bio'
                onChange={(e) => setBio(e.target.value)}
                value={bio} 
                name='bio'
                className='userBio'
                disabled={loading} 
            ></textarea>
            <button className='btn btn-primary' type='submit' disabled={loading}>
                {loading ? 'Updating...' : 'Update Profile'}
            </button>
        </form>
      </div>
    </div>
  );
};

export default UpdateProfile;
